export function jsApipay(that,data){
	console.log(data);
	that.$wx.config({
		debug: true, // ,apialertpclogpc
		appId: data.appId, // 
		timestamp: data.timeStamp, // 
		nonceStr: data.nonceStr, // 
		signature: data.paySign, // 1
		jsApiList: ["chooseWXPay","updateAppMessageShareData","updateTimelineShareData"] // JSJS2
	});
	that.$wx.ready(function() {
		console.log('jweixin.ready');
		that.$wx.checkJsApi({
			jsApiList: ['chooseWXPay',"updateAppMessageShareData","updateTimelineShareData"], // JSJS2,
			success: function(res) {
				console.log('checkjsapi Success')
				console.log(res);
			},
			fail:function(res) {
				console.log('res')
				console.log(res);
			}
		});
		that.$wx.chooseWXPay({
			timestamp: data.timeStamp, // jssdktimestamptimeStampS
			nonceStr: data.nonceStr, //  32 
			package: 'prepay_id=' + data.package, // prepay_idprepay_id=***
			signType: 'MD5', // 'SHA1''MD5'
			paySign: data.paySign, // 
			success:function(res) {
				// 
				console.log('paysuccess')
				console.log(res)
				that.payResult('success')
				
			},
			cancel: function(r) {
				console.log('paycancel')
				console.log(r)
				that.payResult('cancel')
			},
			fail:function(res) {
				that.payResult('fail')
				console.log('payfail')
				console.log(res)
			}
		});
	});
	
	that.$wx.error(function(res) {
		console.log('error')
		console.log(res)
		uni.showToast({
			icon: 'none',
			title: '',
			duration: 4000
		});
			// configerrorconfigdebugresSPA
			/*alert("config");*/
	});
}

export function miniPay(that,data){
	uni.requestPayment({
		provider: 'wxpay',
		timeStamp: data.timeStamp, // jssdktimestamptimeStampS
		nonceStr: data.nonceStr, //  32 
		package: data.package, // prepay_idprepay_id=***
		signType: 'MD5', // 'SHA1''MD5'
		paySign: data.paySign, // 
		success: function (res) {
			console.log('success:' + JSON.stringify(res));
			that.payResult('success')
		},
		fail: function (err) {
			that.payResult('fail')
			console.log('fail:' + JSON.stringify(err));
		}
	});
}

