function getCode(_this, num) {
    _this.reg = true //12
    var remain = num;//
    var time = setInterval(function () {
        if (remain == 1) {
            clearInterval(time);
			_this.second = num
			_this.reg = false
			
            return false      //
        }
        remain--;
        _this.second = remain
    }, 1000)
}
module.exports = {
    getCode                  //js  getCode:getCode
}